import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
// import custom components
import ContainerColSec from '../components/layout/ContainerColSec';
import { HeaderUser } from '../components/basic/Header';
import { TabTwoGroup } from '../components/basic/Tab';

//call api
import { getUserInfo } from '../api/userinfo';
import { useAuth } from '../components/context/AuthContext';
// import { getUserTweets } from '../api/twitter';

const UserFollowersPage = ({ setModalProOpen, setModalTweetOpen }) => {
  const [tabIndex, setTabIndex] = useState('0');
  // const pathId = Number(useParams()); //
  // (pathid) 
  //statetweets like replies state 
  const navigate = useNavigate();
  const { isAuthentical, currentMember } = useAuth();
  // @ local-server 
  const [userInfo, setUserInfo] = useState({});
  //statetweets like replies state 
  // @ tweets  dummy
  // console.log(currentMember)
  //@ profileCard  userInfo

  useEffect(() => {
    const getUserInfoAsync = async () => {
      try {
        const userInfo = await getUserInfo(currentMember.id);
        setUserInfo(userInfo);
      } catch (error) {
        console.error('[getUser Info  with Async failed]', error);
      }
    };
    getUserInfoAsync();
  }, []);

  useEffect(() => {
    if (!isAuthentical) {
      navigate('/login');
    }
  }, [navigate, isAuthentical]); //isAuthenticalnavigation

  //tab
  //swtich case  if elsereturn component(??)
  function switchContext(tabIndex) {
    switch (tabIndex) {
      case '0':
        return <h1> tab0component</h1>;
      default:
        return <h1> tab1component</h1>;
    }
  }

  return (
    <>
      <ContainerColSec
        role='user'
        setModalTweetOpen={setModalTweetOpen}
        setModalProOpen={setModalProOpen}
        pageIndex={1}
        memberId={userInfo.id}
      >
        <section className='section-outer-m col-7'>
          <div className='section-main-m'>
            <HeaderUser userAccountName='John Doe' userTweetsLength='25' />

            <TabTwoGroup tabIndex={tabIndex} setTabIndex={setTabIndex} />

            {switchContext(tabIndex)}
          </div>
        </section>
        <section className='section-right col-3'>
          {/* <FollowCardList /> */}
        </section>
      </ContainerColSec>
    </>
  );
};

export default UserFollowersPage;
